"use client";

import { useState, useEffect, useRef } from "react";
import { Play, AlertCircle, ExternalLink, RefreshCw } from "lucide-react";

interface VideoPlayerProps {
  videoId: string;
  title: string;
}

const VideoPlayer = ({ videoId, title }: VideoPlayerProps) => {
  const [isLoading, setIsLoading] = useState(true);
  const [hasError, setHasError] = useState(false);
  const [showFallback, setShowFallback] = useState(false);
  const [retryCount, setRetryCount] = useState(0);
  const iframeRef = useRef<HTMLIFrameElement>(null);

  // Debug logging
  useEffect(() => {
    console.log('VideoPlayer mounted with videoId:', videoId, 'title:', title);
  }, [videoId, title]);

  // Validate videoId to ensure it's not empty
  if (!videoId || videoId.trim() === '') {
    console.log('VideoPlayer: Invalid videoId provided');
    return (
      <div className="relative w-full aspect-video rounded-xl overflow-hidden bg-blue-50 shadow-blue-lg flex items-center justify-center border border-blue-200">
        <div className="text-center p-8">
          <AlertCircle className="h-12 w-12 text-blue-400 mx-auto mb-4" />
          <p className="text-blue-600 font-medium">Video not available</p>
          <p className="text-xs text-blue-500 mt-2">Video ID: {videoId || 'undefined'}</p>
        </div>
      </div>
    );
  }

  // Set a timeout to show fallback if video doesn't load
  useEffect(() => {
    const timer = setTimeout(() => {
      if (isLoading && !hasError) {
        console.log('VideoPlayer: Timeout reached, showing fallback');
        setShowFallback(true);
        setIsLoading(false);
      }
    }, 8000); // 8 second timeout

    return () => clearTimeout(timer);
  }, [isLoading, hasError, retryCount]);

  const handleLoad = () => {
    console.log('VideoPlayer: iframe loaded successfully');
    setIsLoading(false);
    setHasError(false);
    setShowFallback(false);
  };

  const handleError = (error: any) => {
    console.log('VideoPlayer: iframe error occurred', error);
    setIsLoading(false);
    setHasError(true);
    setShowFallback(false);
  };

  const resetPlayer = () => {
    console.log('VideoPlayer: Resetting player, retry count:', retryCount + 1);
    setHasError(false);
    setIsLoading(true);
    setShowFallback(false);
    setRetryCount(prev => prev + 1);
    
    // Force iframe reload
    if (iframeRef.current) {
      const src = iframeRef.current.src;
      iframeRef.current.src = '';
      setTimeout(() => {
        if (iframeRef.current) {
          iframeRef.current.src = src;
        }
      }, 100);
    }
  };

  // Get the current origin safely
  const getOrigin = () => {
    if (typeof globalThis !== 'undefined' && globalThis.window) {
      return globalThis.window.location.origin;
    }
    return '';
  };

  // Build YouTube URL with privacy-enhanced mode and additional parameters
  const getYouTubeUrl = () => {
    const baseUrl = 'https://www.youtube-nocookie.com/embed';
    const params = new URLSearchParams({
      rel: '0',
      modestbranding: '1',
      enablejsapi: '1',
      origin: getOrigin(),
      autoplay: '0',
      controls: '1',
      disablekb: '0',
      fs: '1',
      iv_load_policy: '3',
      playsinline: '1',
      start: '0',
    });
    
    const url = `${baseUrl}/${videoId}?${params.toString()}`;
    console.log('VideoPlayer: Generated YouTube URL:', url);
    return url;
  };

  // Fallback or error state
  if (hasError || showFallback) {
    return (
      <div className="relative w-full aspect-video rounded-xl overflow-hidden bg-blue-50 shadow-blue-lg flex items-center justify-center border border-blue-200">
        <div className="text-center p-8 max-w-md">
          <AlertCircle className="h-12 w-12 text-blue-500 mx-auto mb-4" />
          <h3 className="text-lg font-semibold text-blue-800 mb-2 font-times">
            {hasError ? "Video Failed to Load" : "Video Loading Issue"}
          </h3>
          <p className="text-blue-600 mb-4 text-sm font-times">
            {hasError 
              ? "There was an error loading this video. This might be due to network issues or video restrictions." 
              : "The video is taking longer than expected to load. You can try refreshing or watch directly on YouTube."
            }
          </p>
          <p className="text-blue-500 mb-4 text-xs">
            Video ID: {videoId} | Retry Count: {retryCount}
          </p>
          <div className="space-y-3">
            <button 
              onClick={resetPlayer}
              className="inline-flex items-center gap-2 px-4 py-2 bg-gradient-to-r from-blue-600 to-blue-700 text-white rounded-md hover:from-blue-700 hover:to-blue-800 transition-all duration-200 shadow-blue-md hover:shadow-blue-lg focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2"
            >
              <RefreshCw className="h-4 w-4" />
              Try Again
            </button>
            <div className="text-center">
              <a
                href={`https://www.youtube.com/watch?v=${videoId}`}
                target="_blank"
                rel="noopener noreferrer"
                className="inline-flex items-center gap-2 text-blue-600 hover:text-blue-800 hover:underline text-sm transition-colors duration-200 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 rounded px-2 py-1"
              >
                Watch on YouTube
                <ExternalLink className="h-4 w-4" />
              </a>
            </div>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="relative w-full aspect-video rounded-xl overflow-hidden bg-blue-50 shadow-blue-lg border border-blue-200">
      {isLoading && (
        <div className="absolute inset-0 flex items-center justify-center bg-gradient-to-br from-blue-50/90 via-white/80 to-blue-100/90 backdrop-blur-sm z-10">
          <div className="text-center">
            <div className="relative mb-4">
              <Play className="h-12 w-12 text-blue-600 mx-auto animate-pulse" />
              <div className="absolute inset-0 bg-gradient-to-r from-blue-400 to-blue-600 rounded-full opacity-20 animate-ping"></div>
            </div>
            <p className="text-blue-700 font-medium font-times">Loading video...</p>
            <p className="text-xs text-blue-500 mt-1">
              Video ID: {videoId}
            </p>
          </div>
        </div>
      )}
      <iframe
        ref={iframeRef}
        key={`${videoId}-${retryCount}`} // Force re-render when videoId changes or retry
        src={getYouTubeUrl()}
        title={title}
        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
        allowFullScreen
        className="absolute inset-0 w-full h-full border-0 rounded-xl"
        loading="lazy"
        referrerPolicy="strict-origin-when-cross-origin"
        sandbox="allow-scripts allow-same-origin allow-presentation allow-forms allow-popups"
        onLoad={handleLoad}
        onError={handleError}
        style={{ 
          colorScheme: 'normal',
          backgroundColor: 'hsl(var(--blue-50))'
        }}
      />
    </div>
  );
};

export default VideoPlayer;