import { FileText } from "lucide-react";
import { ScrollArea } from "@/components/ui/scroll-area";

interface TranscriptPanelProps {
  transcript: string;
  variant?: "default" | "blue";
}

const TranscriptPanel = ({ transcript, variant = "default" }: TranscriptPanelProps) => {
  const paragraphs = transcript.split('\n\n');

  const cardStyles = variant === "blue" ? {
    backgroundColor: 'white',
    borderColor: 'hsl(var(--blue-200))',
    boxShadow: 'var(--blue-shadow-card)'
  } : {};

  const headerStyles = variant === "blue" ? {
    borderBottomColor: 'hsl(var(--blue-200))'
  } : {};

  const iconStyles = variant === "blue" ? {
    color: 'hsl(var(--blue-600))'
  } : {};

  return (
    <div 
      className={`bg-card rounded-xl border border-border overflow-hidden ${
        variant === "blue" ? "focus-within:ring-2 focus-within:ring-blue-500 focus-within:ring-offset-2" : ""
      }`}
      style={cardStyles}
    >
      <div 
        className="p-4 border-b border-border flex items-center gap-2"
        style={headerStyles}
      >
        <FileText 
          className="h-5 w-5 text-primary" 
          style={iconStyles}
        />
        <h3 
          className={`font-semibold ${
            variant === "blue" ? "text-blue-800" : "text-foreground"
          }`}
        >
          Transcript
        </h3>
      </div>
      <ScrollArea className="h-[400px]">
        <div className="p-4 space-y-4">
          {paragraphs.map((paragraph, index) => (
            <p 
              key={`paragraph-${index}`} 
              className={`text-sm leading-relaxed ${
                variant === "blue" ? "text-blue-700" : "text-muted-foreground"
              }`}
            >
              {paragraph}
            </p>
          ))}
        </div>
      </ScrollArea>
    </div>
  );
};

export default TranscriptPanel;