"use client";

import { useState, useEffect } from "react";
import { PenLine, Save } from "lucide-react";
import { Button } from "@/components/ui/button";
import { Textarea } from "@/components/ui/textarea";
import { useToast } from "@/hooks/use-toast";

interface NotesPanelProps {
  lessonId: string;
  variant?: "default" | "blue";
}

const NotesPanel = ({ lessonId, variant = "default" }: NotesPanelProps) => {
  const [notes, setNotes] = useState("");
  const { toast } = useToast();

  const handleSave = () => {
    // In a real app, this would save to the backend
    localStorage.setItem(`lesson-notes-${lessonId}`, notes);
    toast({
      title: "Notes saved",
      description: "Your notes have been saved successfully.",
    });
  };

  // Load notes on component mount
  useEffect(() => {
    const savedNotes = localStorage.getItem(`lesson-notes-${lessonId}`);
    if (savedNotes) {
      setNotes(savedNotes);
    }
  }, [lessonId]);

  const cardStyles = variant === "blue" ? {
    backgroundColor: 'white',
    borderColor: 'hsl(var(--blue-200))',
    boxShadow: 'var(--blue-shadow-card)'
  } : {};

  const headerStyles = variant === "blue" ? {
    borderBottomColor: 'hsl(var(--blue-200))'
  } : {};

  const iconStyles = variant === "blue" ? {
    color: 'hsl(var(--blue-600))'
  } : {};

  return (
    <div 
      className={`bg-card rounded-xl border border-border overflow-hidden ${
        variant === "blue" ? "focus-within:ring-2 focus-within:ring-blue-500 focus-within:ring-offset-2" : ""
      }`}
      style={cardStyles}
    >
      <div 
        className="p-4 border-b border-border flex items-center justify-between"
        style={headerStyles}
      >
        <div className="flex items-center gap-2">
          <PenLine 
            className="h-5 w-5 text-primary" 
            style={iconStyles}
          />
          <h3 
            className={`font-semibold ${
              variant === "blue" ? "text-blue-800" : "text-foreground"
            }`}
          >
            My Notes
          </h3>
        </div>
        <Button 
          size="sm" 
          onClick={handleSave} 
          className={`gap-2 ${
            variant === "blue" 
              ? "bg-blue-600 hover:bg-blue-700 text-white focus:ring-2 focus:ring-blue-500 focus:ring-offset-2" 
              : ""
          }`}
          style={variant === "blue" ? {
            backgroundColor: 'hsl(var(--blue-600))',
            color: 'white'
          } : undefined}
        >
          <Save className="h-4 w-4" />
          Save
        </Button>
      </div>
      <div className="p-4">
        <Textarea
          placeholder="Take notes while watching the lesson..."
          value={notes}
          onChange={(e) => setNotes(e.target.value)}
          className={`min-h-[200px] resize-none bg-background ${
            variant === "blue" 
              ? "border-blue-200 focus:border-blue-500 focus:ring-2 focus:ring-blue-500 focus:ring-offset-2" 
              : ""
          }`}
          style={variant === "blue" ? {
            borderColor: 'hsl(var(--blue-200))'
          } : undefined}
        />
      </div>
    </div>
  );
};

export default NotesPanel;