"use client";

import Link from "next/link";
import { useParams } from "next/navigation";
import { getLessonById } from "@/data/courses";
import LessonSidebar from "@/components/lesson/LessonSidebar";
import VideoPlayer from "@/components/lesson/VideoPlayer";
import TranscriptPanel from "@/components/lesson/TranscriptPanel";
import NotesPanel from "@/components/lesson/NotesPanel";
import { Button } from "@/components/ui/button";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { 
  GraduationCap, 
  ChevronLeft, 
  ChevronRight, 
  CheckCircle,
  Menu,
  X
} from "lucide-react";
import { useState } from "react";
import { cn } from "@/lib/utils";
import { useToast } from "@/hooks/use-toast";

export default function LessonPage() {
  const params = useParams();
  const slug = params.slug as string;
  const lessonId = params.lessonId as string;
  
  const [sidebarOpen, setSidebarOpen] = useState(true);
  const [isCompleted, setIsCompleted] = useState(false);
  const { toast } = useToast();
  
  const result = getLessonById(slug || "", lessonId || "");

  if (!result) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-blue-50">
        <div className="text-center">
          <h1 className="text-2xl font-bold text-blue-800 mb-4">Lesson Not Found</h1>
          <Button asChild className="bg-blue-600 hover:bg-blue-700 text-white">
            <Link href="/courses">Browse Courses</Link>
          </Button>
        </div>
      </div>
    );
  }

  const { course, lesson } = result;
  
  // Find current lesson index and get prev/next
  const currentIndex = course.lessons.findIndex(l => l.id === lesson.id);
  const prevLesson = currentIndex > 0 ? course.lessons[currentIndex - 1] : null;
  const nextLesson = currentIndex < course.lessons.length - 1 ? course.lessons[currentIndex + 1] : null;

  const handleMarkComplete = () => {
    setIsCompleted(!isCompleted);
    toast({
      title: isCompleted ? "Marked as incomplete" : "Lesson completed!",
      description: isCompleted 
        ? "You can continue learning at any time." 
        : "Great job! Keep up the momentum.",
    });
  };

  return (
    <div className="min-h-screen flex flex-col" style={{ backgroundColor: 'hsl(var(--blue-50))' }}>
      {/* Header */}
      <header className="h-16 border-b bg-white flex items-center px-4 gap-4 sticky top-0 z-50 shadow-md" 
              style={{ 
                borderBottomColor: 'hsl(var(--blue-200))',
                boxShadow: 'var(--blue-shadow-md)'
              }}>
        <Button
          variant="ghost"
          size="icon"
          onClick={() => setSidebarOpen(!sidebarOpen)}
          className="lg:hidden transition-all duration-200 hover:scale-105"
          style={{ 
            color: 'hsl(var(--blue-600))',
            '--tw-bg-opacity': '0'
          }}
          onMouseEnter={(e) => {
            e.currentTarget.style.backgroundColor = 'hsl(var(--blue-50))';
            e.currentTarget.style.color = 'hsl(var(--blue-700))';
          }}
          onMouseLeave={(e) => {
            e.currentTarget.style.backgroundColor = 'transparent';
            e.currentTarget.style.color = 'hsl(var(--blue-600))';
          }}
        >
          {sidebarOpen ? <X className="h-5 w-5" /> : <Menu className="h-5 w-5" />}
        </Button>
        
        <Link href="/" className="flex items-center gap-2 hover:opacity-80 transition-all duration-200 hover:scale-105">
          <div className="flex h-9 w-9 items-center justify-center rounded-lg transition-all duration-200 hover:shadow-lg"
               style={{ 
                 backgroundColor: 'hsl(var(--blue-600))',
                 boxShadow: 'var(--blue-shadow-sm)'
               }}
               onMouseEnter={(e) => {
                 e.currentTarget.style.backgroundColor = 'hsl(var(--blue-700))';
                 e.currentTarget.style.boxShadow = 'var(--blue-shadow-md)';
               }}
               onMouseLeave={(e) => {
                 e.currentTarget.style.backgroundColor = 'hsl(var(--blue-600))';
                 e.currentTarget.style.boxShadow = 'var(--blue-shadow-sm)';
               }}>
            <GraduationCap className="h-5 w-5 text-white" />
          </div>
          <span className="font-bold hidden sm:block transition-colors duration-200" 
                style={{ 
                  color: 'hsl(var(--blue-800))',
                  fontFamily: 'Times New Roman, serif'
                }}>
            VBUV-Cloud
          </span>
        </Link>

        {/* Course Navigation Breadcrumb */}
        <div className="flex items-center gap-2 text-sm flex-1 min-w-0">
          <Link 
            href={`/courses/${course.slug}`}
            className="transition-colors duration-200 hover:underline truncate"
            style={{ color: 'hsl(var(--blue-600))' }}
            onMouseEnter={(e) => e.currentTarget.style.color = 'hsl(var(--blue-700))'}
            onMouseLeave={(e) => e.currentTarget.style.color = 'hsl(var(--blue-600))'}
          >
            {course.title}
          </Link>
          <ChevronRight className="h-4 w-4 flex-shrink-0" style={{ color: 'hsl(var(--blue-400))' }} />
          <span className="font-medium truncate" style={{ color: 'hsl(var(--blue-800))' }}>
            {lesson.title}
          </span>
        </div>

        {/* Progress Indicator */}
        <div className="hidden md:flex items-center gap-3">
          <div className="text-xs font-medium" style={{ color: 'hsl(var(--blue-600))' }}>
            {currentIndex + 1} of {course.lessons.length}
          </div>
          <div className="w-20 h-2 rounded-full overflow-hidden" style={{ backgroundColor: 'hsl(var(--blue-100))' }}>
            <div 
              className="h-full rounded-full transition-all duration-300"
              style={{ 
                backgroundColor: 'hsl(var(--blue-600))',
                width: `${((currentIndex + 1) / course.lessons.length) * 100}%`
              }}
            />
          </div>
        </div>

        <Button
          variant={isCompleted ? "default" : "outline"}
          size="sm"
          onClick={handleMarkComplete}
          className={cn(
            "gap-2 transition-all duration-200 hover:scale-105 font-medium",
            isCompleted 
              ? "shadow-md hover:shadow-lg" 
              : "hover:shadow-md"
          )}
          style={isCompleted ? {
            backgroundColor: 'hsl(var(--blue-600))',
            borderColor: 'hsl(var(--blue-600))',
            color: 'white',
            boxShadow: 'var(--blue-shadow-sm)'
          } : {
            borderColor: 'hsl(var(--blue-300))',
            color: 'hsl(var(--blue-600))',
            backgroundColor: 'transparent'
          }}
          onMouseEnter={(e) => {
            if (isCompleted) {
              e.currentTarget.style.backgroundColor = 'hsl(var(--blue-700))';
              e.currentTarget.style.boxShadow = 'var(--blue-shadow-md)';
            } else {
              e.currentTarget.style.backgroundColor = 'hsl(var(--blue-50))';
              e.currentTarget.style.borderColor = 'hsl(var(--blue-400))';
            }
          }}
          onMouseLeave={(e) => {
            if (isCompleted) {
              e.currentTarget.style.backgroundColor = 'hsl(var(--blue-600))';
              e.currentTarget.style.boxShadow = 'var(--blue-shadow-sm)';
            } else {
              e.currentTarget.style.backgroundColor = 'transparent';
              e.currentTarget.style.borderColor = 'hsl(var(--blue-300))';
            }
          }}
        >
          <CheckCircle className={cn(
            "h-4 w-4 transition-all duration-200", 
            isCompleted ? "fill-current text-white" : ""
          )}
          style={!isCompleted ? { color: 'hsl(var(--blue-600))' } : {}} />
          <span className="hidden sm:inline">
            {isCompleted ? "Completed" : "Mark Complete"}
          </span>
        </Button>
      </header>

      {/* Main Layout */}
      <div className="flex-1 flex overflow-hidden">
        {/* Sidebar - Desktop always visible, mobile toggleable */}
        <div
          className={cn(
            "fixed lg:relative inset-y-14 left-0 z-40 lg:z-0 transform transition-transform duration-300 lg:translate-x-0",
            sidebarOpen ? "translate-x-0" : "-translate-x-full"
          )}
        >
          <LessonSidebar 
            course={course} 
            currentLessonId={lesson.id} 
            variant="blue"
            isOpen={sidebarOpen}
            onToggle={() => setSidebarOpen(!sidebarOpen)}
          />
        </div>

        {/* Main Content */}
        <div className="flex-1 overflow-y-auto">
          <div className="max-w-5xl mx-auto p-4 md:p-8 space-y-6 bg-white min-h-full">
            {/* Video Player */}
            <VideoPlayer videoId={lesson.youtubeVideoId} title={lesson.title} />

            {/* Lesson Info */}
            <div className="flex flex-col md:flex-row md:items-center md:justify-between gap-4 p-6 rounded-lg border"
                 style={{ 
                   backgroundColor: 'hsl(var(--blue-50))',
                   borderColor: 'hsl(var(--blue-200))'
                 }}>
              <div className="flex-1">
                <h1 className="text-2xl md:text-3xl font-bold mb-2 transition-colors duration-200" 
                    style={{ 
                      color: 'hsl(var(--blue-800))',
                      fontFamily: 'Times New Roman, serif'
                    }}>
                  {lesson.title}
                </h1>
                <div className="flex flex-col sm:flex-row sm:items-center gap-2 sm:gap-4">
                  <p className="font-medium" style={{ color: 'hsl(var(--blue-600))' }}>
                    Lesson {currentIndex + 1} of {course.lessons.length}
                  </p>
                  <div className="hidden sm:block w-1 h-1 rounded-full" style={{ backgroundColor: 'hsl(var(--blue-400))' }} />
                  <p className="font-medium" style={{ color: 'hsl(var(--blue-600))' }}>
                    {lesson.duration}
                  </p>
                  <div className="hidden sm:block w-1 h-1 rounded-full" style={{ backgroundColor: 'hsl(var(--blue-400))' }} />
                  <p className="text-sm" style={{ color: 'hsl(var(--blue-500))' }}>
                    {course.title}
                  </p>
                </div>
              </div>
              
              {/* Mobile Progress Indicator */}
              <div className="md:hidden">
                <div className="text-xs font-medium mb-2" style={{ color: 'hsl(var(--blue-600))' }}>
                  Course Progress
                </div>
                <div className="w-full h-2 rounded-full overflow-hidden" style={{ backgroundColor: 'hsl(var(--blue-200))' }}>
                  <div 
                    className="h-full rounded-full transition-all duration-300"
                    style={{ 
                      backgroundColor: 'hsl(var(--blue-600))',
                      width: `${((currentIndex + 1) / course.lessons.length) * 100}%`
                    }}
                  />
                </div>
                <div className="text-xs mt-1" style={{ color: 'hsl(var(--blue-500))' }}>
                  {Math.round(((currentIndex + 1) / course.lessons.length) * 100)}% Complete
                </div>
              </div>
            </div>

            {/* Tabs for Transcript and Notes */}
            <Tabs defaultValue="transcript" className="w-full blue-tabs">
              <TabsList 
                variant="blue"
                className="w-full max-w-md p-1 rounded-lg"
              >
                <TabsTrigger 
                  variant="blue"
                  value="transcript" 
                  className="flex-1 rounded-md font-medium transition-all duration-200"
                >
                  Transcript
                </TabsTrigger>
                <TabsTrigger 
                  variant="blue"
                  value="notes" 
                  className="flex-1 rounded-md font-medium transition-all duration-200"
                >
                  My Notes
                </TabsTrigger>
              </TabsList>
              
              <TabsContent variant="blue" value="transcript" className="mt-6">
                <TranscriptPanel variant="blue" transcript={lesson.transcript} />
              </TabsContent>
              <TabsContent variant="blue" value="notes" className="mt-6">
                <NotesPanel variant="blue" lessonId={lesson.id} />
              </TabsContent>
            </Tabs>

            {/* Navigation */}
            <div className="flex items-center justify-between pt-8 border-t"
                 style={{ borderTopColor: 'hsl(var(--blue-200))' }}>
              {prevLesson ? (
                <Button 
                  variant="outline" 
                  asChild 
                  className="gap-2 font-medium transition-all duration-200 hover:scale-105 hover:shadow-md"
                  style={{
                    borderColor: 'hsl(var(--blue-300))',
                    color: 'hsl(var(--blue-600))'
                  }}
                  onMouseEnter={(e) => {
                    e.currentTarget.style.backgroundColor = 'hsl(var(--blue-50))';
                    e.currentTarget.style.borderColor = 'hsl(var(--blue-400))';
                  }}
                  onMouseLeave={(e) => {
                    e.currentTarget.style.backgroundColor = 'transparent';
                    e.currentTarget.style.borderColor = 'hsl(var(--blue-300))';
                  }}
                >
                  <Link href={`/courses/${course.slug}/lesson/${prevLesson.id}`}>
                    <ChevronLeft className="h-4 w-4" />
                    Previous
                  </Link>
                </Button>
              ) : (
                <div />
              )}
              
              {nextLesson ? (
                <Button 
                  asChild 
                  className="gap-2 font-medium transition-all duration-200 hover:scale-105 shadow-md hover:shadow-lg"
                  style={{
                    backgroundColor: 'hsl(var(--blue-600))',
                    color: 'white',
                    boxShadow: 'var(--blue-shadow-sm)'
                  }}
                  onMouseEnter={(e) => {
                    e.currentTarget.style.backgroundColor = 'hsl(var(--blue-700))';
                    e.currentTarget.style.boxShadow = 'var(--blue-shadow-md)';
                  }}
                  onMouseLeave={(e) => {
                    e.currentTarget.style.backgroundColor = 'hsl(var(--blue-600))';
                    e.currentTarget.style.boxShadow = 'var(--blue-shadow-sm)';
                  }}
                >
                  <Link href={`/courses/${course.slug}/lesson/${nextLesson.id}`}>
                    Next Lesson
                    <ChevronRight className="h-4 w-4" />
                  </Link>
                </Button>
              ) : (
                <Button 
                  asChild 
                  className="gap-2 font-medium transition-all duration-200 hover:scale-105 shadow-md hover:shadow-lg"
                  style={{
                    backgroundColor: 'hsl(var(--blue-600))',
                    color: 'white',
                    boxShadow: 'var(--blue-shadow-sm)'
                  }}
                  onMouseEnter={(e) => {
                    e.currentTarget.style.backgroundColor = 'hsl(var(--blue-700))';
                    e.currentTarget.style.boxShadow = 'var(--blue-shadow-md)';
                  }}
                  onMouseLeave={(e) => {
                    e.currentTarget.style.backgroundColor = 'hsl(var(--blue-600))';
                    e.currentTarget.style.boxShadow = 'var(--blue-shadow-sm)';
                  }}
                >
                  <Link href={`/courses/${course.slug}`}>
                    Complete Course
                    <CheckCircle className="h-4 w-4" />
                  </Link>
                </Button>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}