import * as React from "react";
import * as TabsPrimitive from "@radix-ui/react-tabs";

import { cn } from "@/lib/utils";

const Tabs = TabsPrimitive.Root;

const TabsList = React.forwardRef<
  React.ElementRef<typeof TabsPrimitive.List>,
  React.ComponentPropsWithoutRef<typeof TabsPrimitive.List> & {
    variant?: "default" | "blue";
  }
>(({ className, variant = "default", ...props }, ref) => (
  <TabsPrimitive.List
    ref={ref}
    className={cn(
      "inline-flex h-10 items-center justify-center rounded-md p-1 text-muted-foreground",
      variant === "default" && "bg-muted",
      variant === "blue" && "border",
      className,
    )}
    style={variant === "blue" ? {
      backgroundColor: 'hsl(var(--blue-100))',
      borderColor: 'hsl(var(--blue-200))'
    } : undefined}
    {...props}
  />
));
TabsList.displayName = TabsPrimitive.List.displayName;

const TabsTrigger = React.forwardRef<
  React.ElementRef<typeof TabsPrimitive.Trigger>,
  React.ComponentPropsWithoutRef<typeof TabsPrimitive.Trigger> & {
    variant?: "default" | "blue";
  }
>(({ className, variant = "default", ...props }, ref) => {
  const [isActive, setIsActive] = React.useState(false);
  
  return (
    <TabsPrimitive.Trigger
      ref={ref}
      className={cn(
        "inline-flex items-center justify-center whitespace-nowrap rounded-sm px-3 py-1.5 text-sm font-medium ring-offset-background transition-all duration-200 disabled:pointer-events-none disabled:opacity-50",
        variant === "default" && "data-[state=active]:bg-background data-[state=active]:text-foreground data-[state=active]:shadow-sm focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2",
        variant === "blue" && [
          "data-[state=active]:shadow-sm",
          "focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-offset-2"
        ],
        className,
      )}
      style={variant === "blue" ? {
        color: isActive ? 'white' : 'hsl(var(--blue-600))',
        backgroundColor: isActive ? 'hsl(var(--blue-600))' : 'transparent',
        boxShadow: isActive ? 'var(--blue-shadow-sm)' : 'none'
      } : undefined}
      onMouseEnter={(e) => {
        if (variant === "blue") {
          const isCurrentlyActive = e.currentTarget.dataset.state === 'active';
          if (!isCurrentlyActive) {
            e.currentTarget.style.backgroundColor = 'hsl(var(--blue-50))';
            e.currentTarget.style.color = 'hsl(var(--blue-700))';
          } else {
            e.currentTarget.style.backgroundColor = 'hsl(var(--blue-700))';
            e.currentTarget.style.boxShadow = 'var(--blue-shadow-md)';
          }
        }
      }}
      onMouseLeave={(e) => {
        if (variant === "blue") {
          const isCurrentlyActive = e.currentTarget.dataset.state === 'active';
          if (!isCurrentlyActive) {
            e.currentTarget.style.backgroundColor = 'transparent';
            e.currentTarget.style.color = 'hsl(var(--blue-600))';
          } else {
            e.currentTarget.style.backgroundColor = 'hsl(var(--blue-600))';
            e.currentTarget.style.boxShadow = 'var(--blue-shadow-sm)';
          }
        }
      }}
      onFocus={(e) => {
        if (variant === "blue") {
          e.currentTarget.style.outline = 'none';
          e.currentTarget.style.boxShadow = '0 0 0 2px hsl(var(--blue-500))';
        }
      }}
      onBlur={(e) => {
        if (variant === "blue") {
          const isCurrentlyActive = e.currentTarget.dataset.state === 'active';
          e.currentTarget.style.boxShadow = isCurrentlyActive ? 'var(--blue-shadow-sm)' : 'none';
        }
      }}
      {...props}
    />
  );
});
const TabsContent = React.forwardRef<
  React.ElementRef<typeof TabsPrimitive.Content>,
  React.ComponentPropsWithoutRef<typeof TabsPrimitive.Content> & {
    variant?: "default" | "blue";
  }
>(({ className, variant = "default", ...props }, ref) => (
  <TabsPrimitive.Content
    ref={ref}
    className={cn(
      "mt-2 ring-offset-background",
      variant === "default" && "focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2",
      variant === "blue" && [
        "focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-offset-2",
        "focus-visible:ring-blue-500"
      ],
      className,
    )}
    {...props}
  />
));
TabsContent.displayName = TabsPrimitive.Content.displayName;

export { Tabs, TabsList, TabsTrigger, TabsContent };
